<template>
  <div class="container panel has-background-white">
    <h1 class="panel-heading is-4 has-text-centered">
      Consultar as minhas inscrições
    </h1>
    <div class="box">
      <table class="table is-striped is-hoverable is-fullwidth">
        <thead>
          <tr>
            <th>Nome</th>
            <th>Descrição</th>
            <th>Estado</th>
            <th>Início</th>
            <th>Fim</th>
            <th />
            <th />
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="form in forms"
            :key="form.id"
          >
            <td>
              {{ form.title }}
            </td>
            <td align="right">
              <div class="select is-rounded is-fullwidth">
                <select
                  v-model.number="form.type"
                  style="color : black;"
                  :disabled="form.category && (categories[form.category].formsignupid == form.id || categories[form.category].formproposalid == form.id || categories[form.category].formfeedbackid == form.id)"
                  @input="changeType(form.id, form.type == '' ? null : form.type)"
                >
                  <option
                    v-for="(type, key) in types"
                    :key="key"
                    :value="key"
                    style="background-color: white; color: black;"
                  >
                    {{ type.name }}
                  </option>
                </select>
              </div>
            </td>
            <td align="right">
              <div class="select is-rounded is-fullwidth">
                <select
                  v-model.number="form.category"
                  style="color : black;"
                  :disabled="form.category && (categories[form.category].formsignupid == form.id || categories[form.category].formproposalid == form.id || categories[form.category].formfeedbackid == form.id)"
                  @input="changeCategory(form.id, form.category == '' ? null : form.category)"
                >
                  <option />
                  <option
                    v-for="(category, key) in categories"
                    :key="key"
                    :value="key"
                    style="background-color: white; color: black;"
                  >
                    {{ category.name }}
                  </option>
                </select>
              </div>
            </td>
            <td align="center">
              <label
                class="switch"
                style="margin-top: 7px;"
              >
                <input
                  v-if="form.type==1"
                  type="checkbox"
                  :checked="form.category && categories[form.category].formsignupid == form.id"
                  :disabled="form.category && categories[form.category].formsignupid == form.id"
                  @input="changeActive(form.category, form.id, form.type)"
                >
                <input
                  v-if="form.type==2"
                  type="checkbox"
                  :checked="form.category && categories[form.category].formproposalid == form.id"
                  :disabled="form.category && categories[form.category].formproposalid == form.id"
                  @input="changeActive(form.category, form.id, form.type)"
                >
                <input
                  v-if="form.type==3"
                  :checked="form.category && categories[form.category].formfeedbackid == form.id"
                  :disabled="form.category && categories[form.category].formfeedbackid == form.id"
                  type="checkbox"
                  @input="changeActive(form.category, form.id, form.type)"
                >
                <span class="slider round noafter" />
              </label>
            </td>
            <td align="center" />
            <td align="center">
              <a :href="'editInscription/' + form.id">
                <i class="fas fa-edit" />
              </a>
            </td>
            <td align="center">
              <a :href="'deleteInscription/' + form.id">
                <i class="fas fa-trash-alt" />
              </a>
            </td>
          </tr>
        </tbody>
      </table>
    </div>   
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
export default {
  name: "MyInscriptions"
  }
</script>

<style scoped lang="scss">
  .noafter:after {
    content: '' !important; 
  }

  th, td, a {
    vertical-align: middle !important;
  }
</style>
